/*****************************
Login
*****************************/
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration-style: none;
  list-style: none;
}

#root {
  overflow-x: hidden;
}

button:hover, button:focus {
  cursor:pointer;
  outline:none;
}

body {
  /*overflow: auto*/
  overflow:wrap;
}

input:focus {
  outline:none;
}

.errors-container {
  margin-left: 2em;
  background: #FFFFFF;
}

.errors {
  background: #FFFFFF;
}

.error {
  background: #FFFFFF;
  color: red;

}

.LoginPage {
  background: #f6f5f7;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Montserrat', sans-serif;
  height: 100vh;
  margin: -20px 0 50px;
}

.LoginPage h1 {
  font-weight: bold;
  margin: 0;
}

.LoginPage h2 {
  text-align: center;
}

.LoginPage p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

.LoginPage span {
  font-size: 12px;
}

.LoginPage a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.LoginPage button {
  border-radius: 20px;
  border: 1px solid #FF4B2B;
  background: #FF416C;
  background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);
  background: linear-gradient(to right, #FF4B2B, #FF416C);
  color: #FFFFFF;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.LoginPage button:active {
  transform: scale(0.95);
}

.LoginPage button:focus {
  outline: none;
}

.LoginPage button.ghost {
  background-color: #FF416C;
  border-color: #FFFFFF;
}

.LoginPage button.ghost:hover {
  background: #fa7d7d;
  background: -webkit-linear-gradient(to right, #FF4B2B, #fa7d7d);
  background: linear-gradient(to right, #FF4B2B, #fa7d7d);
  box-shadow: 0 1px 5px rgba(145, 92, 182, .4);
}

.LoginPage form {
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100vh;
  text-align: center;
}

.LoginPage input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 25vw;
}

.loginSubmit:focus {
  outline: none;
}


.loginSubmit:hover  {
  box-shadow: 0 1px 5px rgba(145, 92, 182, .4);
  cursor:pointer;
}

.container {
  background-color: #fff;
  border-radius: 10px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
      0 10px 10px rgba(0,0,0,0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
/*  opacity: 0;*/
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}



.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100vh;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container{
  transform: translateX(-100%);
}

.overlay {
  background: #FF416C;
  background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);
  background: linear-gradient(to right, #FF4B2B, #FF416C);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #FFFFFF;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
    transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
    transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100vh;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}


